import React from 'react';
import ReactDataGrid from '@inovua/reactdatagrid-community';
import '@inovua/reactdatagrid-community/index.css';
import NumberFilter from '@inovua/reactdatagrid-community/NumberFilter'
import DateFilter from '@inovua/reactdatagrid-community/DateFilter'
import DateEditor from '@inovua/reactdatagrid-community/DateEditor'
import SelectFilter from '@inovua/reactdatagrid-community/SelectFilter'
import SelectEditor from '@inovua/reactdatagrid-community/SelectEditor'
import CheckBox from '@inovua/reactdatagrid-community/packages/CheckBox'
import SendIcon from '@material-ui/icons/Send';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import { NotificationManager } from 'react-notifications';
import {
  EditButton,
  Button,
  useQueryWithStore,useDataProvider,DateTimeInput
} from 'react-admin';
import moment from 'moment';
import Chip from '@material-ui/core/Chip'
import FilterListIcon from '@material-ui/icons/FilterList';
import { Title } from 'react-admin';
import BoolEditor from '@inovua/reactdatagrid-community/BoolEditor'
import BoolFilter from '@inovua/reactdatagrid-community/BoolFilter'
import DoneIcon from '@material-ui/icons/Done';
import PrintIcon from '@material-ui/icons/Print';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ReactToPrint, { PrintContextConsumer } from 'react-to-print';
import { DaycareToPrint, GroomingPrint } from '../components/printFunc'
import {useSelector } from 'react-redux'
window.moment = moment
var dateFormat = require("dateformat");




export default function BookingView(props) {


 // const theme = useSelector(state => state.admin);
  const dataProvider = useDataProvider();
  const [rows, setRows] = React.useState(props.rows)
  const [selected, setSelected] = React.useState({})
  const [filter, setFilter] = React.useState(false)
  const [anchroEl, setAcnhhorEl] = React.useState(null)
  const componentRef = React.useRef();
  const componentRef1 = React.useRef();
  
  ////CHANGE DATA PROVIDER

  const onEditComplete = async ({ value, columnId, rowIndex, data }) => {
    try {
      if (columnId === 'checkedIn') {


        let nrows = rows
        nrows[nrows.findIndex(i => i.id === data.id)].checkedIn = value
        setRows(nrows)
        await dataProvider.update('bookings', { id: data.id, data: { checkedIn: value } })
      }

      else if (columnId === 'status'){
        let nrows = rows
        nrows[nrows.findIndex(i => i.id === data.id)].status = value
        setRows(nrows)
        await dataProvider.update('bookings', { id: data.id, data: { status: value } })
      }
    }
    catch { }

  }

  const GetService = () => {


 return dataProvider.getList('services', { 'pagination': { "page": 1, "perPage": 100000 } }).then(response => {

      return response.data.map(c => { return { id: c.service, label: `${c.service}-${c.category}` } })
    })
  }
  const GetCategory = () => {
    

    return dataProvider.getList('services', { 'pagination': { "page": 1, "perPage": 100000 } }).then(response => {
      return [...new Set(response.data.map(item => { return item.category}))].map(item=>{return{ id: item, label: item }} )
      //return response.data.map(c => { return { id: c.service, label: `${c.service}-${c.category}` } })
    })
  }

  const columns = [
    {
      name: 'name', header: 'Name', defaultWidth: 200, render: ({ value, data }) => {

        if (data['client_ref']) {

          return (<a href={'#clients/' + data['client_ref']} >{value}</a>)

        }
        else
          return (<span >{value}</span>)

      }, editable: false
    },
    {
      name: 'dogName', header: 'Dog', defaultWidth: 150, render: ({ value, data }) => {
        if (data['dog_ref']) {
          return (<a href={'#dogs/' + data['dog_ref']} >{value}</a>)
        }
        else
          return (<span >{value}</span>)
      }, editable: false
    }, {
      name: 'status', header: 'Status', defaultWidth: 101,
      filterEditor: SelectFilter,
      filterEditorProps: {
        multiple: true,
        wrapMultiple: false,
        dataSource: [{ id: "cancelled", label: "Cancelled" }, { id: "confirmed", label: "Confirmed" }, { id: "modified", label: "Modified" }]
      }, render: ({ value, data }) => {
        if (value === 'confirmed') {
          return (
            <Chip color='primary' style={{ backgroundColor: 'green' }} label='Confirmed' key={value} />
          )
        }
        else if (value === 'modified') {
          return (
            <Chip color='primary' style={{ backgroundColor: 'orange' }} label='Modified' key={value} />
          )
        }
        else if (value === 'cancelled') {
          return (
            <Chip color='primary' style={{ backgroundColor: 'red' }} label='Cancelled' key={value} />
          )
        }
      }, editor: SelectEditor,editorProps: {
        idProperty: 'id',
        dataSource: [{id:'cancelled', label:"Cancelled"}, {id:'modified', label:"Modified"}, {id:'confirmed', label:"Confirmed"}],
        collapseOnSelect: true,
        clearIcon: null
      }

    },
    { name: 'checkedIn', header: 'Checked In', width: 100, filterEditor: BoolFilter, render: ({ value }) => value ? < DoneIcon htmlColor='green' style={{ textAlign: 'center', verticalAlign: 'center' }} /> : null, editor: BoolEditor },
    {
      name: 'service', header: 'Service', defaultWidth: 250,

      filterEditor: SelectFilter,
      filterEditorProps: {
        multiple: true,
        wrapMultiple: false,
        dataSource: GetService(),
      }, editable: false
    },
    {
      name: 'category', header: 'Category', defaultWidth: 150,
      filterEditor: SelectFilter,
      filterEditorProps: {
        multiple: true,
        wrapMultiple: false,
        dataSource: GetCategory(),
      }, editable: false
    },
    {
      name: 'date', header: 'Booking Date', defaultWidth: 138, filterEditor: DateFilter, filterEditorProps: (props, { index }) => {

        return ({
          dateFormat: 'ddd DD/MM/YYYY',
          placeholder: index === 1 ? 'Bookings date is before...' : 'Booking date is on...'
        })
      },
      render: ({ value, cellProps: { dateFormat } }) => {
        return moment(value).format('ddd DD/MM/YYYY')
      }, editable: false
    },
    {
      name: 'start', header: 'Start Time', defaultWidth: 150, filterEditor: DateFilter, filterEditorProps: (props, { index }) => {

        return ({
          dateFormat: 'ddd DD/MM/YYYY',
          placeholder: index === 1 ? 'Bookings date is before...' : 'Booking date is on...'
        })
      },
      render: ({ value, data }) => {

        return `${moment(value).format('hh:mm A')}`
      }
    },
    {
      name: 'addOns', header: 'AddOns', defaultWidth: 180, render: ({ value }) => {
        if (value.length !== 0) {

          return (
            <>
              {value.map(item =>

                <Chip color='secondary' label={item} key={item} />

              )}
            </>
          )

        }
        else
          return (<span></span>)
      },
      cellDOMProps: ({ data, value }) => {
        if (value.length > 2) {
          return {
            onMouseEnter: () => {
              NotificationManager.success(value.map(item => { return <>{item}<br /></> }), 'Extras', 5000, () => { alert(value.map(i => `${i}\n`)) });
            }
          }
        }
      }, editable: false
    },
    { name: 'code', header: 'Booking Code', defaultWidth: 110, editable: false },
  ];

  const gridStyle = { minHeight: 550 };

  const defaultFilterValue = [

    { name: 'date', operator: 'eq', type: 'date', value: '' },
    { name: 'service', operator: 'inlist', type: 'select', value: null },
    { name: 'status', operator: 'inlist', type: 'select', value: null },
    { name: 'category', operator: 'inlist', type: 'select', value: null },
    { name: 'checkedIn', operator: 'eq', type: 'boolean', value: null },
    { name: 'code', operator: 'contains', type: 'string', value: '' },
    { name: 'dogName', operator: 'contains', type: 'string', value: '' },
    { name: 'name', operator: 'contains', type: 'string', value: '' },
  ];


  const handlePrintClick = (event) => {

    setAcnhhorEl(event.currentTarget);
  };

  const handlePrintClose = () => {
    setAcnhhorEl(null);
  };

  React.useEffect(() => {
   
    
    let rows = props.rows
    rows.forEach(i => { i.date = i.start.substring(0, 10); })

    setRows(rows)
  }, [props.rows])






  return (
    <>
      <Title title={props.title} />
      <div>
        <Button
          label="Filters"
          onClick={() => setFilter(!filter)}>
          <FilterListIcon />
        </Button>

        <Button
          label="Print"
          onClick={handlePrintClick}
        >
          <PrintIcon />
        </Button>
        <Menu
          id="simple-menu"
          anchorEl={anchroEl}
          keepMounted
          open={Boolean(anchroEl)}
          onClose={handlePrintClose}
        >
          <ReactToPrint content={() => componentRef.current}>
            <PrintContextConsumer>
              {({ handlePrint }) => (
                <>
                  <MenuItem onClick={handlePrint}>Daycare Attendance List</MenuItem>
                  {Boolean(anchroEl) && <div style={{ display: "none" }}>
                    <DaycareToPrint ref={componentRef} selected={props.title === 'Daycare Today' ? rows : false} />
                  </div>}
                </>
              )}
            </PrintContextConsumer>
          </ReactToPrint>
          <ReactToPrint content={() => componentRef1.current}>
            <PrintContextConsumer>
              {({ handlePrint }) => (
                <>
                  <MenuItem onClick={handlePrint}>Grooming Attendance List</MenuItem>
                  {Boolean(anchroEl) && <div style={{ display: "none" }}>
                    <GroomingPrint ref={componentRef1} selected={props.title === 'Grooming Today' ? rows : false} />
                  </div>}
                </>
              )}
            </PrintContextConsumer>
          </ReactToPrint>

        </Menu>


        {Object.keys(selected).length === 1 && <EditButton
          basePath='/bookings' label='Edit' record={Object.values(selected)[0]}
        />}
        {Object.keys(selected).length === 1 && <Button
          label="Charge card">
          <MonetizationOnIcon />
        </Button>}
        {Object.keys(selected).length !== 0 && <Button
          label="Message Owners">
          <SendIcon />
        </Button>}
      </div>
      <ReactDataGrid
        idProperty="id"
        columns={columns}
        dataSource={rows}
        style={gridStyle}
        onEditComplete={onEditComplete}
        editable={true}
        defaultFilterValue={defaultFilterValue}
        onSelectionChange={select => setSelected(select.selected)}
        enableFiltering={filter}
        multiSelect
        checkboxColumn
      // pagination='local'
      // dataSource={this.handleCallback}
      />
    </>
  )
}
